	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"School 2.0","date":"01 Apr 2006","slug":"/2006-04-01-school-20/","tags":"Y2006"},"html":"<p>Last week Christopher <a href=\"http://www.papascott.de/archives/2006/03/14/previews-of-coming-attractions/\">attended</a> a local school for 3 days to evaluate his fitness to start first grade in the fall in light of his stuttering problem. Officially we had applied for the \"determinination of special educational need\", but actually we wanted a detailed evaulation with an official rejection. Yesterday we a conference with the evaluator, and that's exactly what we got.</p>\n<p>Academically he'll have no problems at all. He breezed through all the \"homework\" he was given, and by the second day was already bored. Psychologically we were told that he is self-confident, charming and somewaht extroverted, and that he talks constantly. None of this was news to us. In short, he has all the prerequisites to do well in a regular elementary school in spite of his stutter. </p>\n<p>We'll arrange a meeting this spring with his future teacher to explain Christopher's situation, but it looks like everything will be set when school starts on 2 September.</p>"},"next":{"fields":{"title":"Where Am I? Who Am I?","date":"25 Mar 2006","slug":"/2006-03-25-where-am-i-who-am-i/"}},"previous":{"fields":{"title":"Weekend in Stralsund","date":"02 Apr 2006","slug":"/2006-04-02-weekend-in-stralsund/"}}},{"node":{"fields":{"title":"Where Am I? Who Am I?","date":"25 Mar 2006","slug":"/2006-03-25-where-am-i-who-am-i/","tags":"Y2006"},"html":"<p>I've been pretty quiet here the past week. Mama was gone 5 days, including last weekend, Christopher had short days for school testing and a home day on Friday, and I've been up to my ears in work. (I installed a new mail server, but the old server died before the all the users were migrated. And that was just one of several problems.)</p>\n<p>But things are looking up. Spring is here, the snow is almost gone. Mama has a business-trip-free April. And the three of us are splitting next weekend to <a href=\"http://www.dorintresorts.com/stralsund\">Stralsund</a>. I'm sure that clear thinking will resume shortly.</p>"},"next":{"fields":{"title":"Poor Sun","date":"20 Mar 2006","slug":"/2006-03-20-poor-sun/"}},"previous":{"fields":{"title":"School 2.0","date":"01 Apr 2006","slug":"/2006-04-01-school-20/"}}},{"node":{"fields":{"title":"Poor Sun","date":"20 Mar 2006","slug":"/2006-03-20-poor-sun/","tags":"Y2006"},"html":"<p>Poor Sun. <a href=\"http://joyeur.com/2006/03/20/the-sun-doesnt-shine-on-me\">They can't sell their servers</a> and <a href=\"http://kalsey.com/2006/03/sun_niagra_server_trial_making_a_strategic_mistake/\">they can't give them away</a>.</p>"},"next":{"fields":{"title":"Robber Hotzenplotz","date":"19 Mar 2006","slug":"/2006-03-19-robber-hotzenplotz/"}},"previous":{"fields":{"title":"Where Am I? Who Am I?","date":"25 Mar 2006","slug":"/2006-03-25-where-am-i-who-am-i/"}}}],"pathPrefix":"","first":false,"last":false,"index":231,"pageCount":1101}}

